.table-column {
  width: 100%;
  /* overflow: scroll; */
}
.select2-container {
  min-width: 60px;
}
.vertical #hot-table {
  width: 100%;
  overflow: scroll;
  height: 400px;
}
.horizontal #hot-table {
  width: 100%;
  overflow: scroll;
  height: 250px;
}
.rule-builder-body {
  height: 400px;
}
.rule-column.vertical {
  height: 400px;
}
.rule-column.horizontal {
  height: 150px;
}
.rules-container-full {
  width: 100%;
  height: 400px;
}
.rules-container {
  border: 1px dashed #ccc;
  padding: 5px;
}
.rules-container-vertical {
  width: 280px;
  height: 400px;
}
.rules-container-horizontal {
  width: 100%;
  height: 150px;
}
.rules-container .title {
  font-weight: bold;
}
.rule-summary {
  height: 100%;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
}
.rule-edit-buttons {
  margin: 5px;
}
.rules {
  -webkit-box-flex: 1;
          flex-grow: 1;
  overflow-y: scroll;
}
.rule-source {
  height: 400px;
}
.rules li {
  list-style-type: circle;
  list-style-position: inside;
  padding: 5px;
  padding-top: 0px;
  padding-bottom: 0px;
}
.rule-column-selector li {
  list-style-type: circle;
  list-style-position: inside;
  padding: 5px;
  padding-top: 0px;
  padding-bottom: 0px;
}
.rules .rule-error {
  display: block;
  margin-left: 10px;
  font-style: italic;
  color: red;
}
.rule-warning {
  display: block;
  margin-left: 10px;
  font-style: italic;
  color: #e28809;
}
.rule-summary .title {
  font-size: 1.1em;
}
.rule-highlight {
  font-style: italic;
  font-weight: bold;
}
.rule-footer-inputs label {
  padding-left: 20px;
  align-self: baseline;
}
.rule-footer-inputs .select2-container {
  align-self: baseline;
}
.rule-footer-inputs {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
          justify-content: space-between;
  flex-wrap: wrap;
  -webkit-box-align: baseline;
          align-items: baseline;
}
.rule-footer-inputs input {
  align-self: baseline;
}
.extension-select {
  -webkit-box-flex: 1;
          flex: 1;
  max-width: 120px;
  min-width: 60px;
}
.genome-select {
  -webkit-box-flex: 1;
          flex: 1;
  max-width: 300px;
  min-width: 120px;
}
.collection-name {
  -webkit-box-flex: 1;
          flex: 1;
  min-width: 120px;
  max-width: 500px;
}
.rule-footer-genome-group {
  -webkit-box-flex: 2;
          flex: 2;
  display: -webkit-box;
  display: flex;
}
.rule-footer-extension-group {
  -webkit-box-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: flex;
}
.rule-footer-name-group {
  -webkit-box-flex: 3;
          flex: 3;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
          flex-direction: row-reverse;
}
.fa-edit,
.fa-times,
.fa-wrench {
  cursor: pointer;
}
