/* Can not be scoped because of command line tdClass */
.break-word {
  white-space: pre-wrap;
  word-break: break-word;
}
.info-frame-container {
  overflow: hidden;
  padding-top: 100%;
  position: relative;
}
.info-frame-container iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
