.bold {
  font-weight: bold;
}
.light {
  font-weight: lighter;
  color: grey;
}
.right-aligned {
  text-align: right;
}
.clickable {
  cursor: pointer;
}
.workflow {
  border: solid gray 1px;
  margin-bottom: 1%;
}
.workflow > .header {
  background: lightgray;
  padding: 5px 10px;
}
.workflow > .light {
  color: gray;
}
.workflow > .body {
  border-top: solid gray 1px;
}
.workflow > .body > .tool {
  border: 0px;
  margin: 0;
}
div.tool {
  margin-bottom: 5px;
  border: 1px solid gray;
}
.tool > .header {
  padding: 5px 10px;
}
.job-inputs {
  margin: 0px 6px 0px 6px;
  text-align: left;
}
.job-inputs td:nth-child(1) {
  text-align: right;
  font-weight: lighter;
  color: gray;
}
.job-inputs td:nth-child(1):after {
  content: ":";
}
.job-inputs td:nth-child(2) {
  padding-left: 4px;
}
.job-inputs-show {
  float: right;
}
.copied-from {
  border: 1px solid lightgrey;
  border-width: 1px 1px 0px 1px;
  margin-bottom: 5px;
}
.copied-from .header {
  border-bottom: 1px solid lightgrey;
  padding: 5px;
}
.copied-from .header .bold,
.copied-from .header a {
  color: #888;
}
